.about {
	margin-top: 3rem;
	font-family: 'Montserrat', sans-serif;
	color: #0a0908;
}

#headshot {
	margin: 0 auto;
}

.links  {
	
	font-family: 'Montserrat', sans-serif;
	color: #0a0908;
	margin-top: 2rem;
	padding: 1rem;
}

.title {
	text-align: center;
	margin: 0 0 1rem;
	font-size: 1.5em;
	border-bottom: 1px solid #bf132a;
}

.links p {
	text-align: center;
	margin:0;
}

.siteLinks {
	background-color: #18435a;
	position: relative;
	margin: -8rem 0 0;
	font-family: 'Montserrat', sans-serif;
	color: #f7f7ff;
	text-align: center;
	z-index: 50;
	padding: 1rem 2rem;
}

.site {
	font-size: 1.5em;
	font-weight: 600;
	margin: 0;
	border-bottom: 1px solid #f7f7ff;
	padding-bottom: .7rem;
	font-family: "Cinzel", serif;
}

h3.site a, h4.repo a {
	color: #f7f7ff;
	text-decoration: none;
}

.repo {
	font-family: 'Montserrat', sans-serif;
	font-size: 1em;
	margin: 0;
	padding-top: .2rem;
}

.image {
	position: relative;
	z-index: 0;
}

.tile {
	width: 300px;
	margin-left: 8px;
	margin-bottom: 1rem;
}

input[type="submit"].button-primary:disabled, input[type="submit"].button-primary:hover:disabled {
	font-family: 'Montserrat', sans-serif;
	cursor: not-allowed;
	background-color: #aaa;
}

input[type="submit"].button-primary {
	font-family: 'Montserrat', sans-serif;
	background-color: #50c9ce;
	border-color: #50c9ce;
}
input[type="submit"].button-primary:hover {
	background-color: #43a9ad;
}

.contact {
	font-family: 'Montserrat', sans-serif;
	color: #0a0908;
	margin-top: 2rem;
}

.contact h3 {
	border-bottom: 1px solid #bf132a;
}

.thanks {
	text-align: center;
	height: 4rem;
	background-color: rgba(122,155,118,0.3);
	padding-top: 2rem;
	border-radius: 5px;
}
#navbar {
	background-color: #18435a;
	height: 7rem;
}

h1 {
	margin:0;
}

a.navMainLink {
	font-family: "Cinzel", serif;
	color: #f7f7ff;
	text-decoration: none;
}

#toggler {
	display: none;
}

.link a{
	font-family: "Cinzel", serif;
	color: #f7f7ff;
	text-decoration: none;
	font-size: 1.5em;
} 

.first {
	padding-left: 4rem;
}

.contain {
	width: 41px;
	border: 1.8px solid #f7f7ff;
	border-radius: 5px;
	margin-top: 15px;
	cursor: pointer;
}

.line {
	width: 35px;
	height: 3px;
	background-color: #f7f7ff;
	margin: 4px 3px;
	border-radius: 2px;
}

.line-top {
	margin-top: 7px;
}

.line-bottom {
	margin-bottom: 7px;
}

.link a.active {
	font-size: 1.7em;
	color: #dcdde2;
	font-weight: 500;
}
